.Contact_card{
    margin: 30px 100px;
    border-radius: 20px;
    background: linear-gradient(95deg, #0087AB 23.03%, #FFF 98.04%);
    padding: 30px 0px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    position: relative;
    z-index: 20;
}
.Contact_card h1{
    color: #FFF;
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    
}
.Contact_card p{
    color: #FFF; 
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    margin: 10px 0px;
    line-height: 35px;
}
.Contact_card_1{
    width: 588px;
}
.Contact_card_1_img{
    height: 200px;
    width: 180px;
}
.footer{
    background-color: black;
    height:auto;
    width:auto; 
    margin-top: -200px;
}
.footer_icon{
    display: flex;
    justify-content: center;
    padding: 200px 0px 0px 0px;
}
.footer_icon span{
    width: 100px;
    height: 70px;
    flex-shrink: 0;
}
.footer_hr{
    display: flex;
    justify-content: center;
}
.footer_hr hr{
    width: 908px;
    height: 1px;
    background: #0087AB;
    border: none;
    margin: 0px 120px;
}
.footer_list{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 10px 0px 0px 0px;
}
.footer_list p{
    color: #FFF;
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    padding: 10px 20px;
}
.footer_list a{
    text-decoration: none;
    color: #FFF;
}
.footer_list hr{
    width: 1px;
    height: 22.204px;
    background: #FFF;
    margin: 10px;
}
.footer_Copyrights{
    border-radius: 50px 50px 0px 0px;
    background: rgba(0, 135, 171, 0.44);
    padding: 12px;
    text-align: center;
}
.footer_Copyrights a{
    color: #FFF;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-decoration: none;
    
}
.footer_Copyrights span{
    color: #FFF;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

@media (min-width: 368px) and (max-width: 720px){
    .Contact_card {
        margin: 20px 10px;
        background: linear-gradient(95deg, #0087AB 23.03%, #5a5a5a 98.04%);
        padding: 30px;
    }
    .Contact_card h1{
        margin: 10px 0px;
    }
    .Contact_card_1{
        width: auto;
    }
    .footer_icon span{
        width: 80px;
    }
}