    .Products{
        margin: 30px 0px 0px 0px;
    }
    .products_h1{
        text-align: center;
        font-size: 55px;
        font-weight: 500;
    }
    .Products_container{
        margin: 20px 100px;
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }
    .Products_card{
        background-color: #f4f8fe;
        margin: 20px 50px;
        border-radius: 10px;
        padding: 10px;
        width: 300px;
        display: flex;
        flex-direction: column;
        align-items: center;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    }
    .Products_card_img{
        height: 250px;
        width: 200px;
    }
    .Products_card_1_h1{
        font-size: 20px;
        font-weight: 400;
        margin: 20px;
    }
    .Products_card_1_p{
        font-size: 20px;
        font-weight: 400;
        margin: 2px 20px;
    }
    .Products_card_1_btn{
        margin: 10px 20px;
        padding: 10px;
        background-color: rgb(42, 177, 255);
        border: none;
        border-radius: 5px;
        color: #fff;
        cursor: pointer;
    }
    .popup_message {
        position: fixed;
        top: 20px;
        right: 20px;
        background-color: #28a745;
        color: white;
        padding: 10px 20px;
        border-radius: 5px;
        font-weight: bold;
        z-index: 1000;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
        transition: opacity 0.3s ease;
    }
    

    @media (min-width: 368px) and (max-width: 720px){
        .products_h1{
            font-size: 40px;
        }
        .Products_container{
            margin: 20px 0px;
        }
    }


