.About_Section {
  width: auto;
  height:70vh  ;
  flex-shrink: 0;
  background: linear-gradient(
    99deg,
    rgba(0, 135, 171, 0.74) -0.71%,
    rgba(0, 54, 69, 0.74) 98.97%
  );
  padding: 50px;
  display: flex;
  justify-content: space-between;
}
.about_icon {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.about_center {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}
.about_center_h1 {
  color: #fff;
  font-family: "Leckerli One";
  font-size: 80px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.about_center_card {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.about_center_card_1 {
  width: 423px;
  height: auto;
  flex-shrink: 0;
  border-radius: 15px;
  background: #d1d1d1;
  padding: 20px;
  margin: 40px;
}
.about_center_card_1 p {
  color: #000;
  text-align: center;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.about_center_card_2 {
  width: 469.916px;
  height: auto;
  flex-shrink: 0;
  border-radius: 15px;
  background: rgba(255, 231, 147, 0.68);
  padding: 20px;
  margin: 40px;
}
.about_center_card_2 p {
  color: #000;
  text-align: center;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.about_btn_card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.about_btn_card button {
  border-radius: 10px;
  background: #000;
  backdrop-filter: blur(2px);
  border: none;
  width: 180px;
  height: 45px;
  flex-shrink: 0;
  color: #fff;
  text-align: center;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 10px 80px;
}
.about_btn_card img {
  width: 280px;
  height: 222px;
  flex-shrink: 0;
}

.about_text {
  color: #d1d1d1;
  font-family: "Kavoon", cursive;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}


.about_us{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    margin: 30px 100px;
    position: relative;
}
.about_us_img {
    width: 250px;
    height: 250px;
    border-radius: 40px;
    /* border: solid 5px #003645; */
    z-index: 10;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}
.about_us_p{
    font-size: 18px;
    margin: 20px 50px;
    line-height: 40px;
    letter-spacing: 1.2px;
    color: #858585;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    z-index: 10;
}


.about_count{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin: 30px 0px;
}
.about_count_card{
    width: 244px;
    height: 147px;
    flex-shrink: 0;
    border-radius: 20px;
    background: #003645;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.about_count_card h1{
    color: #FFF;
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
.about_count_card p{
    color: #FFF;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.about_team{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.about_team_h1{
    text-align: center;
    margin: 20px 0px;
    color: #000;
    font-size: 70px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
.about_team_span{
    width: 277.45px;
    height: 66.183px;
    margin-top: -20px;
}
.about_team_cards{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-bottom: 30px ;
}
.about_team_card{
    width: 250px;
    height: 342px;
    flex-shrink: 0;
    background: #0087AB;
    padding: 10px;
    border-radius: 300px 0px 0px 0px;
    border: 5px solid #767676;
    background: #0087AB;
    box-shadow: 10px 10px 4px 0px rgba(0, 0, 0, 0.25);
}
.about_team_card img{
    width: 150px;
    height: 150px;
    flex-shrink: 0;
    border-radius: 200px;
    border: 5px solid #FFF;
    background-color: orange;
}
.about_team_card_1{
  text-align: center;
}
.about_team_card_1 h1{
  color: #F69F40;
  font-size: 40px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.about_team_card_1 p{
  color: #FFF;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 10px 0px;
}
.about_team_card_1_icon{
  margin: 25px 0px;
}


@media (min-width: 720px) and (max-width: 1200px){ 
  .About_Section{
    height: 80vh;
  }
  .about_center_card_1{
    width: auto;
    margin: 0px;
  }

}
@media (min-width: 368px) and (max-width: 720px){ 
  .About_Section{
    padding: 10px 0px;
  }
  .about_center_card{
    justify-content: start;
  }
  .about_center_card_1{
    width: 320px;
    margin: 20px;
  }
  .about_center_card_2{
    width: 320px;
    margin: 20px;
  }
  .about_text{
    display: none;
  }
  .about_icon{
    display: none;
  }
  .about_btn_card img{
    display: none;
  }
  .about_us{
    margin: 20px 0px; 
  }
  .about_count_card{
    margin: 20px 0px;
  }
}
