/* Navbar styles */
.Navbar {
    background: linear-gradient(122deg, #1f1f1f 0%, #007BFF 100%);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 80px;
    position: relative;
    z-index: 1000;
}


.logo img{
    height: 30px;
    width: auto;
}

.menu {
    list-style: none;
    display: flex;
    transition: transform 0.3s ease-in-out;
}

.menu li {
    margin: 0px 30px;
    font-size: 20px;
    font-weight: 500;
}

.menu li a {
    text-decoration: none;
    color: white;
}

.hamburger {
    display: none;
    flex-direction: column;
    cursor: pointer;
}

.bar {
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 4px 0;
}

/* Responsive design */
@media (max-width: 768px) {
    .menu {
        position: fixed;  /* Make the menu fixed */
        top: 0;
        right: 0;
        background-color: rgba(6, 6, 6, 0.9);
        height: 50vh;   /* Make the menu cover full screen height */
        width: 60vw;     /* Set a specific width for the menu */
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transform: translateX(100%); /* Hidden by default */
        z-index: 2000;
    }

    .menu.active {
        transform: translateX(0); /* Visible when active class is added */
    }

    .menu li {
        margin: 20px 0;
    }

    .hamburger {
        display: flex;
    }

    /* Prevent scrolling when the menu is open */
    body.menu-open {
        overflow: hidden;
    }
}

@media (max-width: 480px) {
    .Navbar {
        padding: 20px;
    }

    .menu {
        height: 50vh;
    }

    .menu li {
        margin: 10px 0;
        font-size: 18px;
    }
}
