.deal_btn{
    background-color: rgb(21, 21, 21);
    padding: 15px;
    border: none;
    margin-top: 20px;
    border-radius: 5px;
    color: white;
    font-size: 15px;
    cursor: pointer;
}


.Card{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.deal_card{
    border-radius: 10px;
    color: white;
    margin-top: 80px;
    width: 320px;
}
.deal_card_img{
    height: 200px;
    width: 320px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
.deal_card_1{
    background-color: rgb(24, 24, 24);
    margin-top: -5px;
    padding: 10px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}
.deal_card_1 h1{
    font-size: 22px;
    font-weight: 400;
}
.deal_card_1 p{
    margin:10px 0px ;
    font-size: 13px;
}

