.contact_us_section {
  width: auto;
  height: 90vh;
  flex-shrink: 0;
  background: #003645;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact_us_h1 {
  color: #fff;
  font-family: "Leckerli One";
  font-size: 70px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: center;
  margin: 20px;
}
.contact_us_card {
  width: auto;
  height: 450px;
  flex-shrink: 0;
  border-radius: 50px;
  background: #d9d9d9;
  display: flex;
  flex-wrap: wrap;
}
.contact_us_card_img {
  border-radius: 50px 0px 0px 50px;
  border: none;
  width: 450px;
}
.contact_us_card_list {
  display: flex;
  flex-direction: column;
  justify-content: start;
  padding: 10px 80px;
}
.contact_us_card_list label {
  font-size: 18px;
}
.contact_us_card_list input {
  margin: 8px 0px;
  padding: 10px;
  border: none;
  border-radius: 50px;
  width: 300px;
}
.contact_us_card_list textarea {
  padding: 15px;
  border: none;
  border-radius: 20px;
  width: 290px;
  height: 70px;
  margin-top: 10px;
}
.contact_us_card_list button {
  width: 141px;
  height: 39px;
  flex-shrink: 0;
  border-radius: 50px;
  background: #003645;
  color: #fff;
  border: none;
  margin-top: 20px;
  cursor: pointer;
}

.cart {
  background-color: #eb7231;
  padding: 20px;
  color: #d400ff;
  width: 80%;
}
.cart_section {
  margin-top: 20px;
}

.cart_section h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.cart_section ul {
  list-style-type: none;
  padding: 0;
}

.cart_section li {
  background-color: #f9f9f9;
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 5px;
}


.cart-details{
    background-color: #003645;
    margin: 0px 100px;
    border-radius: 0 0 30px 30px;
    padding: 10px 80px;
}
.details_h3{
    text-align: center;
    color: white;
    font-size: 35px;
    font-weight: 400;
}
.details_p{
    padding: 10px 40px;
    font-size: 20px;
    color: white;
}
.details_ul{
    list-style:none;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
 
}
.details_li{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 25px 0px;
    background-color: #F4F8FE;
    padding: 20px;
    width: 40%;
    border-radius: 10px;
}
.details_img{
    height: 150px;
    width: 120px;
}
.details_list{
    display: flex;

    align-items: start;
    justify-content: center;
    padding: 20px;
}
.details_list p{
  padding: 8px 0px;
  font-size: 20px;
}


@media (min-width: 368px) and (max-width: 720px){
    .contact_us_section{
        height: auto;
    }
    .contact_us_card{
        height: auto;
    }
    .contact_us_card_list{
        padding: 10px 45px;
    }
    .contact_us_card_img{
        border-radius: 50px 50px 0 0;
        width: 412px;
    }
    .contact_us_card{
        margin-bottom: 20px;
    }
    .cart-details{
        margin: 0px 20px;
        padding: 10px 0px;
    }
    .details_li {
        width: 60%;
    }
}


