*{
    padding: 0px;
    margin: 0px;
}

.banner_section{
    background-image: url('../images/Rectangle\ 6.png');  
    background-position: center; 
    background-size: cover; 
    background-repeat: no-repeat;
    height: 420px;
    width: auto;
    padding: 50px;
    margin: 00px 0px 40px 0px;
    /* border-radius: 20px; */
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: start;
    overflow: hidden;
}
.banner_h1{
    color: #FFF;
    text-align: center;
    font-family: 'Leckerli One', cursive;
    font-size: 70px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    width: 25vw;
}
.banner_p{
    color: #FFF;
    text-align: center;
    font-size: 25px;
    font-style: normal;
    font-weight: 200;
    line-height: normal;
    margin: 20px 0px;
    padding-left: 50px;
}
.banner_btn{
    width: 150px;
    height: 42px;
    flex-shrink: 0;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.737);
    backdrop-filter: blur(2px);
    color: #FFF;
    text-align: center;
    font-family: 'Itim', cursive;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    border: none;
    margin: 20px 0px;
    cursor: pointer;
}

.banner_card_1{
    color: #FFF;
    text-align: center;
    font-family: "Irish Grover";
    font-size: 35px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.banner_img{
    width: 443px;
    height: 337px;
    flex-shrink: 0;
}
.banner_span_1{
    position: absolute;
    bottom: 60px;
    right: 0px;
    color: #FFF;
    font-family: 'Gloria Hallelujah', cursive;
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    transform: rotate(-31.014deg);

}
.banner_span_2{
    position: absolute;
    top: 60px;
    right: 30%;
    color: #FFF;
    font-family: 'Gloria Hallelujah', cursive;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.banner_span_3{
    position: absolute;
    width: 180px;
    height: 135.201px;
    flex-shrink: 0;
    left: 80px;
}

.sub_cards{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.sub_card_1{
    height: 180px;
    flex-shrink: 0;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background: linear-gradient(122deg, #007BFF 0%, #FFF 100%);
    display: flex;
    justify-content: space-around;
}

.sub_card_1 h1{
    padding: 10px ;
    color: #FFF;
    font-family: "Inria Sans",cursive;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}
.sub_card_1 h2{
    font-family: "Inria Sans",cursive;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    padding: 20px 10px ;
    color: #fff;
}
.sub_card_1 p{
    color: #FFF;
    text-align: start;
    font-family: 'Itim', cursive;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding: 10px;
}
.sub_card_1_img{
    height: 150px;
    margin-top: 10px;
}
.sub_card_2{
    width: 310px;
