.ProductDetail{
    margin: 50px 200px;
}
.ProductDetail h1{
    font-size: 40px;
    font-weight: 500;
}
.ProductDetail_card{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 50px;
}
.ProductDetail_card_1{
    display: flex;
    flex-direction: column;
    /* display: block; */
}
.ProductDetail_card_1 img{
    height: 120px;
    width: 100px;
}
.ProductDetail_card_2 img{
    height: 350px;
    width:  320px;
}
.ProductDetail_card_2 h2{
    font-size: 25px;
    font-weight: 400;
}
.ProductDetail_card_2 p{
    margin-top: 10px;
    font-size: 16px;
}
.ProductDetail_card_2 h3{
    background-color: #2AB1FF;
    padding: 8px;
    font-size: 18px;
    font-weight: 500;
    margin-top: 12px;
    width: 130px;
    border-radius: 5px;
    color: white;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.ProductDetail_card_2_1{
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-around;
}
.ProductDetail_card_2_1 button{
    margin-top: 12px;
    font-size: 16px;
    font-weight: 500;
    padding: 10px;
    background-color: rgb(42, 177, 255);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    border: none;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
}

@media (min-width: 368px) and (max-width: 720px){
    .ProductDetail{
        margin: 10px 20px;
    }
    .ProductDetail h1{
        font-size: 25px;  
    }
    .ProductDetail_card_1{
        display: flex;
        flex-direction: row;
    }
    .ProductDetail_card_1 img{
        height: 100px;
        width: 80px;
    }
}
